


.root {
    min-width: 18vw;
    height: 100vh;
    padding:80px 10px;
    background-color: rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    gap: 10px;
}




.link {
    border-radius: 12px;
    cursor: pointer;
    padding: 20px 40px;
    background-color: rgba(160, 160, 160, 0.229);
    color: white;
    font-size: 20px;
    font-weight: 500;
    
    &:hover {
        background-color: rgba(212, 212, 212, 0.229);
    }

    &__active {
        background-color: rgba(151, 151, 151, 0.425);
        &:hover {
            background-color: rgba(151, 151, 151, 0.425);
        }
    }
}

